import { Component, OnInit } from '@angular/core';
import { AppService } from '../../shared/service/app.service';
import { RestService } from '../../common/rest.service';

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['../landingpage/landingpage.component.css']
})
export class AboutComponent implements OnInit {

  constructor( private appService:AppService, private restService:RestService) {
    sessionStorage.clear();
    this.appService.sessionUserEmit(null);
  }

   ngOnInit(){
    sessionStorage.clear();
    this.appService.sessionUserEmit(null);
  }
    landingpage(){
    this.appService.navigate('landingpage',[])
  }
    login(){
    this.appService.navigate('sign/signin',[])
  }
  signup(){
    this.appService.navigate('sign/signup',[])
  }
  pricing(){
    this.appService.navigate('../pricing',[]) 
  }
  about(){
    this.appService.navigate('../about',[])
  }
  contact(){
    this.appService.navigate('../contact',[])
  }

}



